html {
  font-family: sans-serif;
}

a {
  color: #030393;
  text-decoration: none;
}

section p {
  text-align: center;
  background-color: #91cf02;
  margin-bottom: 10px;
  padding: 10px;
}

button, #btn {
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #68cd09;
  border: none;
  border-radius: 3rem;
  justify-self: flex-start;
  max-width: 20rem;
  max-height: 6rem;
  padding: 5px 18px;
  font-size: 3rem;
}

#btn {
  z-index: 3;
  transition: all 2s;
  position: fixed;
  top: auto;
  bottom: 10px;
  right: 10px;
}

.btn-click {
  animation-name: down;
  animation-duration: 2s;
}

form {
  flex-direction: column;
  display: flex;
}

form div label {
  text-align: right;
  color: #044b17;
  width: 9rem;
  font-size: larger;
  display: inline-block;
}

form div input {
  color: green;
  border: 3px solid #49da0a;
  border-radius: 2rem;
  outline-color: #273b02;
  width: 60%;
  margin-block-end: 1rem;
  font-size: 1.5rem;
}

#img-otra {
  flex-wrap: wrap;
  justify-content: center;
  display: flex;
}

#img-otra figure, #img-otra figure img {
  object-fit: cover;
  border-radius: 2rem;
  max-width: 640px;
}

#img-otra figure {
  animation-name: fadetr;
  animation-duration: 4s;
}

@keyframes fadetr {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes down {
  0% {
    opacity: .8;
    transform: translateY(30px);
  }

  25% {
    opacity: .9;
    transform: translateY(0);
  }

  50% {
    opacity: .9;
    transform: translateY(30px);
  }

  100% {
    opacity: .8;
    transform: translateY(0);
  }
}

@media (width <= 640px) {
  #img-otra {
    width: 100%;
  }

  figure, img {
    width: 100%;
    height: auto;
  }

  form div, form {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    display: flex;
  }

  form div label {
    width: -moz-fit-content;
    width: fit-content;
  }

  form div input {
    width: 80%;
  }
}

